html{height:100%;overflow-y:scroll}body{height:100%;padding-right:20px;margin:0;font-size:14px;background-color:#daded4}
.main{display:flex;flex-flow:column wrap;min-height:100vh;width:100%}.header{display:flex;flex-direction:row;justify-content:space-between;align-items:center}.header__title{padding-left:30px}.header__menuBtn{display:none}.subSection{flex-grow:1;display:flex;flex-flow:row nowrap;height:100%;width:100%}.changesection{width:100%}.nav{display:flex;flex-direction:column;align-items:center;align-self:center;width:150px;height:100%;margin-left:30px;font-family:"Sarala",sans-serif;font-size:24px;flex-basis:auto}.nav__link{color:#3c403d;text-decoration:none;width:100%;text-align:center}.nav__link:hover{color:#000;font-weight:bolder;background:url(data:image/png;base64,);background-repeat:no-repeat;background-size:auto;background-position:center}.socialMedia{display:flex;flex-direction:column;align-items:center;align-self:center;height:100%}.socialMedia>*{padding-bottom:10px}@media(max-width: 960px){.nav{display:none}.header__menuBtn{display:inline;margin-right:50px}.header__menuBtn__icon{cursor:pointer}.shortNav{display:flex;flex-direction:column;text-align:center;transition-delay:1s}.socialMedia{display:flex;flex-direction:column;justify-content:space-between;align-self:start;height:50%;padding-top:120px}.socialMedia>*{padding-bottom:10px}}@media(max-width: 700px){.subSection{flex-grow:0;display:flex;flex-flow:column wrap;height:100%;width:100%}.header__menuBtn{display:inline;margin-right:0px}.socialMedia{display:flex;flex-direction:row;justify-content:center;align-items:center;align-self:start;width:100%;padding-top:0px}.socialMedia>*{padding:10px 5px}}
.content{height:100%;padding-left:100px;display:flex;flex-direction:row;justify-content:center;align-items:center}.content__name{font-size:58px;font-family:"Mate",serif;max-width:700px;align-self:center}.content__buttons>*{margin-right:15px}.btn-1{font-family:inherit;background:#3c403d;color:#fff;cursor:pointer;font-size:30px;padding:1.5rem;border:0;transition:all .5s;border-radius:10px;width:auto;position:relative}.btn-1::after{content:"";font-family:"FontAwesome";font-weight:400;position:absolute;left:85%;top:31%;right:5%;bottom:0;opacity:0}.btn-1:hover{background:#111;transition:all .5s;border-radius:10px;box-shadow:0px 6px 15px #59595a61;padding:1.5rem 3.5rem 1.5rem 1.5rem}.btn-1:hover::after{opacity:1;transition:all .5s}.login-btn{font-family:inherit;background:#39603d;color:#fff;cursor:pointer;font-size:30px;padding:1.5rem;border:0;transition:all .5s;border-radius:10px;width:auto;position:relative}.login-btn::after{content:"";font-family:"FontAwesome";font-weight:400;position:absolute;left:86%;top:54%;right:0;bottom:0;opacity:0;transform:translate(-50%, -50%)}.login-btn:hover{background:#48656e;transition:all .5s;border-radius:10px;box-shadow:0px 6px 15px #59595a61;padding:1.5rem 3rem 1.5rem 1.5rem;color:#fff}.login-btn:hover::after{opacity:1;transition:all .5s;color:#fff}#face{transform-box:fill-box;transform-origin:center;-webkit-animation:face 2s linear 0s infinite;animation:face 2s linear 0s infinite;z-index:1}#face:hover{-webkit-animation:faceHover 1s ease-in-out 0s infinite;animation:faceHover 1s ease-in-out 0s infinite}@-webkit-keyframes face{0%{transform:rotateZ(0deg)}25%{transform:rotateZ(5deg)}50%{transform:rotateZ(0deg)}75%{transform:rotateZ(-5deg)}100%{transform:rotateZ(0deg)}}@keyframes face{0%{transform:rotateZ(0deg)}25%{transform:rotateZ(5deg)}50%{transform:rotateZ(0deg)}75%{transform:rotateZ(-5deg)}100%{transform:rotateZ(0deg)}}@-webkit-keyframes faceHover{0%{transform:rotateZ(0deg)}25%{transform:rotateZ(10deg)}50%{transform:rotateZ(0deg)}75%{transform:rotateZ(-10deg)}100%{transform:rotateZ(0deg)}}@keyframes faceHover{0%{transform:rotateZ(0deg)}25%{transform:rotateZ(10deg)}50%{transform:rotateZ(0deg)}75%{transform:rotateZ(-10deg)}100%{transform:rotateZ(0deg)}}@media(max-width: 960px){.content{height:100%;padding-left:50px;display:flex;flex-direction:row;justify-content:center;align-items:flex-start}.content__name{font-size:58px;font-family:"Mate",serif;padding:0 15px;max-width:700px;align-self:flex-start}}@media(max-width: 700px){.content{display:flex;flex-direction:row;flex-wrap:wrap}.content__name{font-size:58px;font-family:"Mate",serif;padding:0 0px;max-width:700px;align-self:flex-start}}
